import React, {Component} from 'react';
import TimesheetHome from '../timesheet/TimesheetHome'

class Home extends React.Component {
  submit = (values) => {
    console.log("data values:#################################################################"+values)
  }
  render() {
    return(
      <TimesheetHome onSubmit ={this.submit} />
    )
  }

}

export default Home

